import pic1 from "../static/Photos/icons8-html-5.svg";
import pic2 from "../static/Photos/icons8-css3.svg";
import pic3 from "../static/Photos/icons8-javascript.svg";
import pic4 from "../static/Photos/icons8-react-native.svg";
import pic5 from "../static/Photos/icons8-git.svg";
import pic6 from "../static/Photos/icons8-python.svg";
import pic7 from "../static/Photos/Vue.js_Logo_2.svg";
