import "./App.css";
import Navbar from "./Componants/Navbar";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.min.js";
import About from "./Componants/About";
import MoreAboutMe from "./Componants/MoreAboutMe";
import MyPastEducation from "./Componants/MyPastEducation";
import MyPastEducation2 from "./Componants/MyPastEducation-copy";
import MyTopSkills from "./Componants/MyTopSkills";
import MyRecentProjects from "./Componants/MyRecentProjects";
import MyRecentProjects2 from "./Componants/MyRecentProjects-copy";
import ContactMe from "./Componants/ContactMe";
import DarkModeButton from "./Componants/DarkModeButton";
import ModalAlert from "./Componants/Modal";
import { useState } from "react";
import "aos/dist/aos.css";
import React from "react";
import ProgressBar from "react-scroll-progress-bar";
import AOS from "aos";
import { useEffect } from "react";
import { HashRouter as Router, Routes, Route } from "react-router-dom";

// import Copyright from './Componants/Copyright';

function App() {
  const d = new Date();
  let hour = d.getHours();
  const [active, setActive] = useState(0);
  const [aosTog, setAosTog] = useState(false);
  const [darkMode, setDarkMode] = useState(false);

  const toggleAosTog = () => {
    setAosTog(!aosTog);
  };
  useEffect(() => {
    AOS.init(
      {
        duration: 300,
        offset: 300,
        // once: true
      },
      []
    );
  });

  window.onload = function () {
    if (hour >= 20 || hour < 6) {
      setDarkMode(true);
    }
  };

  const dark = darkMode ? "dark" : "light";
  const toggleDarkMode = () => {
    setDarkMode(!darkMode);
  };
  return (
    <Router>
      <div
        className={
          dark + " transition main crsrdflt" + (aosTog ? " inactive" : "")
        }
      >
        <ModalAlert />
        <Navbar darkMode={darkMode} active={active} setActive={setActive} />
        <Routes>
          <Route
            index
            element={
              <>
                <div className="pt-3">
                  <About />
                  <MoreAboutMe setActive={setActive} />
                  <MyPastEducation />
                  <MyTopSkills />
                  <MyRecentProjects />
                  <ContactMe />
                </div>
              </>
            }
          />

          <Route
            path="about"
            element={
              <>
                <div className="pt-3">
                  <MoreAboutMe setActive={setActive} />
                  <MyPastEducation2 />
                </div>
              </>
            }
          />

          <Route
            path="skills"
            element={
              <>
                <div className="pt-3">
                  <MyTopSkills />
                  <MyRecentProjects2 />
                </div>
              </>
            }
          />

          <Route
            path="contact-me"
            element={
              <>
                <div className="pt-3">
                  <ContactMe />
                </div>
              </>
            }
          />
        </Routes>

        <DarkModeButton
          darkMode={darkMode}
          toggleAosTog={toggleAosTog}
          toggleDarkMode={toggleDarkMode}
        />
        {/* <Copyright /> */}
        <ProgressBar bgcolor={darkMode ? "#5f697c" : "#f75b00"} height="3px" />
      </div>
    </Router>
  );
}

export default App;
