export default function DarkModeButton(props) {
  return (
    <>
      <div className="darkAosDiv">
        <div className="text-start">
          <button
            onClick={() => {
              props.toggleAosTog();
            }}
            className="button3 ms-2 mb-2"
          >
            Animations
          </button>
        </div>

        <div className="text-end">
          <button
            onClick={() => {
              props.toggleDarkMode();
            }}
            className="button3 me-2 mb-2"
          >
            Dark Mode
          </button>
        </div>
      </div>
    </>
  );
}
