import { HashLink } from "react-router-hash-link";
function MoreAboutMe(props) {
  return (
    <div className="container">
      <h2 data-aos="zoom-in-up" className="text-center mt-4">
        About Me
      </h2>
      <div className="row justify-content-start mb-5 mt-4">
        <div className="col">
          <p data-aos="zoom-in-up" className="p1">
            I’m Happy Jangir, but you can just call me Happy.
            <br />
          </p>
          <p data-aos="zoom-in-up" className="p1">
            Originally hailing from India for the last decade or so I’ve been
            honing my skills in design, production and management roles building
            projects for the web, mobile, games and more.
            <br />
          </p>
          <p data-aos="zoom-in-up" className="p1">
            I strive to create elegant solutions that surprise and delight
            users, while keeping complex technical dependencies in mind for
            implementation, scalability and developer sanity.
            <br />
          </p>
          <p data-aos="zoom-in-up" className="p1">
            I’ve worked with small, agile teams on skunkworks projects and
            larger development teams with projects.
            <br />
          </p>
          <p data-aos="zoom-in-up" className="p1">
            Have an interesting project?
            <HashLink
              onClick={() => {
                props.setActive(0);
              }}
              to="/#contact-us"
              className="ps-2 lnk"
            >
              Let's talk
            </HashLink>
            .
          </p>
        </div>
      </div>
    </div>
  );
}

export default MoreAboutMe;
