import { useState } from "react";
import { Instagram } from "react-bootstrap-icons";
import { Facebook } from "react-bootstrap-icons";
import { Twitter } from "react-bootstrap-icons";
function About() {
  const [sbmt, setSbmt] = useState(false);
  return (
    <>
      <div className="container">
        <h2 data-aos="zoom-in-down" id="ctct" className="text-center mt-4">
          Let's talk.
        </h2>
        <p data-aos="zoom-in-down" className="text-center pt-4 mb-0 p1">
          Get in touch via the form below, or by emailing{" "}
          <a className="lnk" href="mailto:hjangir2003@gmail.com">
            hjangir2003@gmail.com
          </a>
          .
        </p>

        <div
          data-aos="zoom-in-down"
          className="row pb-4 pt-5 justify-content-between text-center text-sm-start"
        >
          <div className="col-12 col-sm-5">
            <form
              onSubmit={(e) => {
                e.preventDefault();
                console.log("Hello");
                setSbmt(true);
                setTimeout(() => {
                  window.scrollTo(0, document.body.scrollHeight);
                }, 30);
              }}
            >
              <div className="mb-3">
                <label htmlFor="name" className="form-label d-none">
                  Name
                </label>
                <input
                  required
                  placeholder="YOUR NAME"
                  type="text"
                  className="form-control txtbox"
                  id="name"
                />
              </div>

              <div className="mb-3">
                <label
                  htmlFor="exampleInputEmail1"
                  className="form-label d-none"
                >
                  Email address
                </label>
                <input
                  required
                  placeholder="YOUR EMAIL ADDRESS"
                  type="email"
                  className="form-control"
                  id="Email"
                  aria-describedby="emailHelp"
                  name="Email"
                />
              </div>

              <div className="form-group">
                <label
                  className="form-label d-none"
                  htmlFor="exampleFormControlTextarea1"
                >
                  Message
                </label>
                <textarea
                  required
                  placeholder="YOUR MESSAGE"
                  className="form-control"
                  id="exampleFormControlTextarea1"
                  rows="3"
                  name="Message"
                ></textarea>
              </div>
              <button id="sbmt" type="submit" className="btn mt-3 button2">
                Submit
              </button>
              {sbmt ? (
                <div className="col-lg-12">
                  <div className="sent-message mt-3">
                    Your message has been sent. Thank you!
                  </div>
                </div>
              ) : (
                ""
              )}
            </form>
          </div>

          <div className="uppercase col-12 col-sm-5 text-center text-sm-end mt-5 mt-sm-0">
            <ul className="contact-details">
              <li>
                <span>126, Mukundgarh Road </span>
              </li>
              <li>
                <span>Antroli, Laxmangarh</span>
              </li>
              <li>
                <span>+91 7877019481</span>
              </li>
              <li>
                <span>hjangir2003@gmail.com</span>
              </li>
            </ul>
            <div className="group mt-3">
              <h1>
                <a
                  className="SocMedLnk"
                  id="insta"
                  href="https://www.instagram.com/happy_jangir_2003/"
                  target="_blank"
                  rel="noreferrer"
                >
                  <Instagram />
                </a>
                <a
                  className="SocMedLnk"
                  id="facbok"
                  href="https://www.facebook.com/happy.jangir.355/"
                  target="_blank"
                  rel="noreferrer"
                >
                  <Facebook />
                </a>
                <a
                  className="SocMedLnk"
                  id="twitr"
                  href="https://twitter.com/jangir_happy_"
                  target="_blank"
                  rel="noreferrer"
                >
                  <Twitter />
                </a>
              </h1>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default About;
