import { Link } from "react-router-dom";

export default function Navbar(props) {
  const dark = props.darkMode ? "dark" : "light";

  return (
    <>
      <nav
        id="navbar"
        className={
          "navbar navbar-expand-sm navbar-" + dark + " transition bg-" + dark
        }
      >
        <div className="container">
          <a
            className="navbar-brand"
            href="/#"
            onClick={() => {
              props.setActive(0);
            }}
          >
            Happy Jangir
          </a>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-sm-0 pt-3 pt-sm-0">
              <li className="nav-item">
                <Link
                  className={"nav-link" + (props.active === 0 ? " active" : "")}
                  aria-current="page"
                  to="/"
                  onClick={() => {
                    props.setActive(0);
                  }}
                >
                  Home
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  className={"nav-link" + (props.active === 1 ? " active" : "")}
                  to="/about"
                  onClick={() => {
                    props.setActive(1);
                  }}
                >
                  About
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  className={"nav-link" + (props.active === 2 ? " active" : "")}
                  to="/skills"
                  onClick={() => {
                    props.setActive(2);
                  }}
                >
                  Skills
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  className={"nav-link" + (props.active === 3 ? " active" : "")}
                  to="/contact-me"
                  onClick={() => {
                    props.setActive(3);
                  }}
                >
                  Contact
                </Link>
              </li>
            </ul>
            <form className="d-flex"></form>
          </div>
        </div>
      </nav>
    </>
  );
}
