import { click } from "@testing-library/user-event/dist/click";
import { useEffect } from "react";
function ModalAlert() {
  useEffect(() => {
    click(document.getElementById("modal1"));
  }, []);

  return (
    <>
      <button
        type="button"
        className="btn btn-primary d-none"
        data-bs-toggle="modal"
        data-bs-target="#exampleModal"
        id="modal1"
      >
        Launch demo modal
      </button>

      <div
        className="modal fade"
        id="exampleModal"
        tabIndex="-1"
        aria-labelledby="exampleModalLabel"
        // aria-hidden="false"
      >
        <div className="modal-dialog">
          <div className="modal-content bg-dark">
            <div className="modal-header text-start d-block">
              <h5 className="modal-title">Welcome</h5>
            </div>
            <div className="modal-body">
              Hello, welcome to this website.{" "}
              {window.innerWidth < 450
                ? "Please use a Desktop device for a better experience."
                : "Please use a mobile device for a better experience."}{" "}
              <br />
              Click anywhere on the screen or the close button to close the
              welcome message.
            </div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-secondary closeModal"
                data-bs-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default ModalAlert;
