import Maze from "../static/Photos/images.jpeg";
function MyRecentProjects() {
  const projctclsses = "col-sm-3 pt-3 mb-5 mb-md-0 hover";
  return (
    <>
      <div className="container">
        <h2 data-aos="zoom-in-up" className="text-center">
          Some of the Recent Projects
        </h2>
        <div className="row justify-content-evenly mb-5 mt-5 ps-3 pe-3 text-center">
          <div data-aos="zoom-in-up" className={projctclsses}>
          <img className="maze" src={Maze} alt="" />
            <p>I will add certain projects soon.. Currently I'm working on some projects.</p>
          </div>

          <div data-aos="zoom-in-up" className={projctclsses}>
            <img className="maze" src={Maze} alt="" />
            <p>Created a <b>Maze Generator</b> with the help of P5.js and JavaScript.</p>
          </div>

          <div data-aos="zoom-in-up" className={projctclsses}>
          <img className="maze" src={Maze} alt="" />
            <p>I have certain projects to add. I will add the projects here shortly.</p>
          </div>
        </div>
      </div>
      <span id="contact-us"></span>
      <span id="contact-us"></span>
    </>
  );
}

export default MyRecentProjects;
